//****************************************
// Skills
//****************************************

.section-skills {
  background: white;
  padding: 100px 40px;

  h2 {
    color: $color-base-lighter;
    margin: 0;
  }
  .list-skills {
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
      display: inline;
      padding: 0 5px 0 0;
      margin: 0 5px 0 0;
      border-right: 1px solid black;
    }
  }

  @include breakpoint(phablet) {
    grid-template-columns: repeat(8, 1fr);
    padding: 50px 0;

    &--title {
      grid-column: 2/4;
      grid-row: 1;
      align-self: normal;
    }

    &--copy {
      grid-column: 4/8;
      grid-row: 1;
      padding-top: 120px;
    }
  }
}
