.section-work {
  display: block;
  position: relative;
  color: $color-contrast;
  padding: 15em 1.5em;
  overflow: hidden;
  p {
    font-size: 15px;
  }
}
.section-work--copy {
  position: relative;
  z-index: 1;
  h2 {
    padding-left: 0px;
  }
  p {
    padding: 0 0 40px 20px;
    font-size: 1.3em;
    line-height: 1.6em;
  }
}

.section-work--image {
  grid-column: 2/6;
  grid-row: 1;
  justify-self: left;
  width: 100%;
  position: relative;
  .work-image--bg {
    position: absolute;
    max-height: 400px;
    img {
      max-width: 100%;
      object-fit: cover;
      opacity: 0.7;
    }
  }
  .work-image--fg {
    padding-top: 100px;
  }
}
//alt layout
.section-work.copy-right {
  .section-work--copy {
    grid-column: 1/5;
    grid-row: 1;
  }
  .section-work--image {
    grid-column: 4/8;
    grid-row: 1;

    .phone {
      left: 50%;
    }
    .laptop {
      left: 50%;
    }
  }
}

//project Skills
.tabs--skills {
  list-style-type: none;
  margin: 10px 0 0 0;
  padding: 0;

  li {
    display: inline-block;
    font-size: 10px;
    text-transform: uppercase;
    margin: 0;
    padding: 0;

    &:after {
      content: " /";
    }

    &:last-child {
      &:after {
        content: "";
      }
    }

    &.title {
      background-color: $color-base-lighter;
      color: $color-base;
      padding-left: 5px;
      padding-right: 8px;
      margin-right: 5px;

      &:after {
        content: "";
      }
    }
  }
}
//Project Challenges
.tabs--challenges {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 15px;

  li {
    padding: 10px 0 10px 20px;
    position: relative;
    line-height: 22px;
    border-bottom: 1px solid $color-base-lighter;

    &:before {
      content: ">";
      @extend .font-headline;
      font-size: 20px;
      position: absolute;
      left: 0;
    }
  }
}
//project CTA button
.btn {
  border: 1px solid $color-contrast;
  color: $color-contrast;
  text-decoration: none;
  padding: 10px 20px;
  margin: 15px 0;
  display: inline-block;
  transition: all 0.2s ease;

  &:hover,
  &:focus {
    background-color: $color-contrast;
    color: $color-base;
  }
}
//SVG outline shapes
.work-image--shape {
  top: 20px;
  position: absolute;
  svg path {
    fill: transparent;
    stroke: $color-contrast;
    fill: transparent;
  }
}

.section-work {
  &:nth-child(1) {
    .work-image--shape {
      transform: translate(-10px, -10px);
    }
  }
  &:nth-child(2) {
    .work-image--shape {
      transform: rotate(180deg) translate(60px, -10px);
    }
  }
  &:nth-child(3) {
    .work-image--shape {
      transform: rotate(45deg) translate(150px, 0px);
    }
  }
}

@include breakpoint(tablet) {
  .section-work {
    display: grid;
    padding: 200px 0;
  }
  .section-work--copy {
    grid-column: 4/8;
    grid-row: 1;
    padding-left: 10px;
    h2 {
      padding-left: 60px;
    }
    p {
      padding-left: 100px;
      font-size: 1.3em;
      line-height: 1.6em;
    }
  }

  .section-work--image {
    .work-image--bg {
      position: relative;
    }
    .work-image--fg {
      padding-top: 0;
    }
  }
  //section base copy container
  .section-work--base {
    grid-column: 3/7;
    grid-row: 2;
  }
}
