//****************************************
// Contact
//****************************************
.section-contact {
  position: relative;
  display: block;
  padding: 0 40px;
  h2 {
    color: $color-contrast;
    opacity: 0.2;
  }

  &--copy {
    &--body {
      padding-left: 40px;
      a {
        @extend h3;
        color: $color-contrast;
        img {
          margin-right: 10px;
          position: relative;
          top: 5px;
        }
      }
    }
  }

  &--anim {
    .anim-wave {
      float: right;
      width: 200px;
      height: 150px;
    }
  }

  @include breakpoint(tablet) {
    display: grid;
    &--copy {
      grid-column: 2/6;
      align-self: middle;
      padding-bottom: 100px;
    }
    &--image {
      grid-column: 5/7;
      position: relative;
      z-index: 2;
    }
    &--anim {
      grid-column: 7/-1;
      grid-row: 1;
      align-self: end;
    }
  }
}
