.wrapper-laptop {
  margin-bottom: 75px;
  @include breakpoint(tablet) {
    margin-bottom: 0;
  }
}
.laptop {
  position: relative;
  top: 0%;
  left: -10%;
  width: 100%;
  max-width: 600px;
  height: 0;
  padding-top: 50%;

  @include breakpoint(tablet) {
    left: -20%;
  }

  &--slideshow {
    background: black;
    position: absolute;
    width: 80%;
    height: 100%;
    top: 9%;
    left: 10%;
    overflow: hidden;
    img {
      position: absolute;
      top: 0;
      left: 0;
      object-fit: cover;
      width: 100% !important;
    }
  }
  &--macbook {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: auto !important;
  }

  @include breakpoint(tablet) {
    width: 400px;
    height: 232px;
    &--slideshow {
      width: 310px;
      height: 200px;
      top: 26px;
      left: 48px;
    }
  }
}
